.navigation {
  text-transform: uppercase;
  pointer-events: none;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  background: $navigation-bg;
  font-weight: 600;

  &__logo {
    @include small-tablet {
      width: 130px;
      height: 38px;
    }
  }


  &__phone-number,
  &__call-to-order {
    text-decoration: none;
    color: $navigation-color;
  }

  &__phone-number {
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 27px;
  }

  &__call-to-order {
    display: block;
    width: max-content;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    border-bottom: 1px solid #1d1d1d;
    padding-bottom: 8px;
    margin-top: 16px;
  }

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__link {
    display: inline-block;
    text-decoration: none;
    color: $navigation-color;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
  }

  &__link::after {
    display: block;
    content: "";
    height: 1px;
    background: $navigation-color;
    margin-top: 8px;
    opacity: 0;
  }

  &__link:hover::after {
    opacity: 1;
  }


  &__menu {
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding: 0;
    margin: 48px 0 40px;
    list-style: none;
  }
}
